import React from 'react'
import { Link } from 'react-scroll'
import styles from './Navigation.module.css'

function Navigation() {
  return (
    <nav className={styles.glass}>
        <div className={styles.navList}>
            <Link activeClass="active" to="test1" spy={true} smooth={true} offset={50} duration={500}>API WEATHER</Link>
            <Link activeClass="active" to="test1" spy={true} smooth={true} offset={50} duration={500}>Coming soon</Link>
            <Link activeClass="active" to="test1" spy={true} smooth={true} offset={50} duration={500}>Coming soon</Link>

        </div>
    </nav>
  )
}

export default Navigation