import './App.css';
import React, { useState } from 'react';
import axios from 'axios';

import Navigation from './components/Navigation';
import Search from './components/search/Search';
import CurrentWeather from './components/current-weather/current';
import { WEATHER_API_KEY, WEATHER_API_URL } from './components/api';
import Forecast from './components/forcast/forecast';

function App() {
  const [currentWeather, setCurrentWeather] = useState(null);
  const [forecastWeather, setForecastWeather] = useState(null);


  const handleOnSearchChange = (searchData) => {
    const [lat, lon] = searchData.value.split(' ');

    const currentWeatherFetch = fetch(`${WEATHER_API_URL}/weather?lat=${lat}&lon=${lon}&appid=${WEATHER_API_KEY}&units=metric`);

    const forecastWeatherFetch = fetch(`${WEATHER_API_URL}/forecast?lat=${lat}&lon=${lon}&appid=${WEATHER_API_KEY}&units=metric`)

    Promise.all([currentWeatherFetch, forecastWeatherFetch])
      .then(async (response) => {
        const weatherResponse = await response[0].json();
        const forecastResponse = await response[1].json();

        setCurrentWeather({city: searchData.label, ...weatherResponse});
        setForecastWeather({city: searchData.label ,...forecastResponse});
      })
      .catch((err) => console.log(err))
  }
console.log(currentWeather);

  return (
    <div className="App">
      <Navigation />
      <Search onSearchChange={handleOnSearchChange} />
      <div className='flex clrwt'>
        { currentWeather && <CurrentWeather data={currentWeather}/>}
        {forecastWeather && <Forecast data={forecastWeather}/>}
      </div>

    </div>
  );
}

export default App;
