import React from 'react'


const CurrentWeather = ({ data }) => {
    const wind = data.wind.deg;
    let windDeg = '';
    if (wind > 0 && wind < 41) {
        windDeg = 'Nort'
    } else if (wind > 40 && wind < 141) {
        windDeg = 'East'
    } else if (wind > 140 && wind < 221) {
        windDeg = 'South'
    } else if (wind > 220 && wind < 321) {
        windDeg = 'West'
    } else windDeg = 'Nort'

    console.log(wind)

    return (
        <div className='current'>
            <h2 className='bold fz40'>{data.city}</h2>
            <img
                className='weather_icon'
                src='../../images/01d.png' />
            <div className='weater_description bold'>
                {data.weather[0].description}
            </div>
            <div className='current__parametrs'>
                <div className='temperature'>
                    <p className='small'>Temperature</p>
                    <p className='bold'>{Math.round(data.main.feels_like)} &#176; C</p>
                </div>
                <div className='feelsLike'>
                    <p className='small'>Feels like</p>
                    <p className='bold'>{Math.round(data.main.temp)} &#176; C</p>
                </div>
                <div className='wind'>
                    <p className='small'>Wind</p>
                    <p className='wind_speed bold'>{data.wind.speed} m/s {windDeg}</p>
                </div>


            </div>
        </div>
    )
}
